import { openPage } from "./paginator";

function openPreview(e) {
    e.preventDefault();
    e.stopPropagation();
    const card = e.currentTarget;
    const previewWrapper = document.querySelector('[data-previewWrapper]');
    const previewTitle = document.querySelector('[data-previewTitle]');
    const previewImage = document.querySelector('[data-previewImage]');
    const previewLink = document.querySelector('[data-previewLink]');
    const resultImage = document.querySelector('[data-resultImage]');
    const resultLink = document.querySelector('[data-resultLink]');

    previewTitle.innerText = card.dataset.title;
    previewImage.src = resultImage.src = card.querySelector('img').src;
    previewLink.href = resultLink.href = card.querySelector('a').href;
    previewWrapper.hidden = false;
}

document.querySelectorAll('.cards-grid .card').forEach(card => {
    card.addEventListener('click', openPreview);
})

document.querySelectorAll('[data-previewWrapper] button').forEach(button =>
    button.addEventListener('click', () => {
        const previewWrapper = document.querySelector('[data-previewWrapper]');
        previewWrapper.hidden = true;
    })
)
